import React, {useState, useEffect} from 'react'
import { FaBars } from 'react-icons/fa';
import { 
    Nav, 
    NavbarContainer, 
    NavLogo, 
    MobileIcon, 
    NavMenu, 
    NavItem, 
    NavLinks,
    NavBtn, 
    NavBtnLink 
} from './NavbarElements';
import hikedBlue from '../../images/hikedBlue.jpeg';
import hikedBlack from '../../images/hikedBlack.jpeg';
import {animateScroll as scroll} from 'react-scroll';


const Navbar = ({ toggle }) => {

    const [scrollNav, setScrollNav] = useState(false);

    const [logo, setLogo] = useState(hikedBlue);

    const changeNav = () => {
        if(window.scrollY >= 10)
        {
            setScrollNav(true);
            setLogo(hikedBlack)
        }
        else
        {
            setScrollNav(false);
            setLogo(hikedBlue)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav);
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                    <NavLogo to='/' onClick={toggleHome}>
                        <div width='50px' height='80px'>
                            <img alt='hiked' src={logo} width='50px' height='auto'></img>
                        </div>
                    </NavLogo>
                    <MobileIcon onClick={toggle}>
                        <FaBars/>
                    </MobileIcon>
                    <NavMenu>
                        <NavItem>
                            <NavLinks to='about' smooth={true} duration={500}spy={true} exact='true' offset={-80}
                            // activeClass='active'
                            >About</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='discover' smooth={true} duration={500}spy={true} exact='true' offset={-80}>Discover</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='services' smooth={true} duration={500}spy={true} exact='true' offset={-80}>Services</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='signup' smooth={true} duration={500}spy={true} exact='true' offset={-80}>Sign Up</NavLinks>
                        </NavItem>
                    </NavMenu>
                    <NavBtn>
                        <NavBtnLink to='/signin'>Sign In</NavBtnLink>
                    </NavBtn>
                </NavbarContainer>
            </Nav>    
        </>
    )
}

export default Navbar;
