import React from 'react';
import {
    Container, 
    FormWrapp, 
    Icon, 
    FormContent, 
    Form, 
    FormH1, 
    FormLabel, 
    FormInput, 
    FormButton, 
    Text} 
    from './SigninElements'
import hiked from '../../images/hiked.jpeg';

const Signin = () => {
    return (
        <>
           <Container>
               <FormWrapp>
                   <Icon to='/'>
                    <img alt='hiked' src={hiked} width='50px' height='auto'></img>
                   </Icon>
                   <FormContent>
                       <Form action='#'>
                           <FormH1>Sign in to your account</FormH1>
                           <FormLabel htmlFor='for'>Email</FormLabel>
                           <FormInput type='email' required/>
                           <FormLabel htmlFor='for'>Password</FormLabel>
                           <FormInput type='password' required/>
                           <FormButton type='submit'>Continue</FormButton>
                           <Text>Forgot password</Text>
                       </Form>
                   </FormContent>
               </FormWrapp>
           </Container>
        </>
    )
}

export default Signin;
